@import "../styles/variable.scss";

// pc处理
@media screen and (min-width: 900px) {
  .farm-pc {
    display: block;
  }
  .farm-wap {
    display: none !important;
  }
  .farm-table-pc{
    display: table-cell;
  }
  .farm-table-wap {
    display: none !important;
  }

  .farms {
    .container {
      width: 100%;
      padding: 8px 16px 0px 16px;
      flex-grow: 1;

      .box {
        margin-bottom: 20px;

        .title {
          height: 65px;
          line-height: 65px;
          font-family: Montserrat-BlackItalic;
          font-size: 16px;
          align-items: center;
          font-weight: 400;
          padding-left: 20px;
        }

        .table {
          width: 100%;
          display: table;
          border-spacing: 0;
          border-collapse: separate;

          tr,
          .tr {
            color: inherit;
            display: table-row;
            outline: 0;
            vertical-align: middle;
          }
          
          th,
          .td {
            top: 0;
            left: 0;
            z-index: 2;
            text-align: center;
            font-weight: 500;
            line-height: 1.5rem;
            display: table-cell;
            padding: 10px 10px;
            font-size: 0.875rem;
            font-weight: 400;
            line-height: 1.43;
            border-bottom: 1px solid rgba(224, 224, 224, 1);
          }
        }

        .op-box {
          display: flex;
          width: 100%;
          min-height: 0px;
          height: auto;
          transition-duration: 307ms;
          transition: height 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;

          .op-box-shadow {
            box-shadow: 0px 2px 1px -1px #00000033, 0px 1px 1px 0px #00000024,
              0px 1px 3px 0px #0000001f;
          }

          .op-box-cell {
            flex: 1;
            min-width: 320px;
            padding: 10px 20px;
            border-radius: 0px;
            height: 100%;
            box-sizing: border-box;
            text-align: left;
            .title {
              padding: 5px;
              font-size: 17px;
            }
            .subtitleBox {
              display: flex;
              .subtitle {
                font-size: 0.875rem;
                padding: 5px;
                flex: 1;
                text-align: left;
              }
            }
            .btn {
              color: rgba(0, 0, 0, 0.87);
              box-shadow: none;
              background: $grad-btn-bg;
              margin-top: 5px;
              border: none;
              width: 100%;
              padding: 6px 16px;
              font-size: 0.875rem;
              min-width: 64px;
              box-sizing: border-box;
              border-radius: 3px;
              &.disable {
                background-color: #d4b6b4;
                color: rgba(0, 0, 0, 0.26);
              }
            }
            .link {
              text-decoration: underline;
              color: lightblue;
              cursor: pointer;
              float: right;
            }
            .warp {
              width: 100%;
              position: relative;
              display: flex;
              .left {
                flex: 1;
                width: 100%;
              }
              .right {
                width: 60px;
                float: right;
                margin-left: 10px;
                align-items: center;
                .icon {
                  font-size: 1.5rem;
                  margin-top: 8px;
                }
              }
            }
          }
        }
      }
    }
  }
}

